import React, { Component } from "react";
import img from "./cont.png";
import * as contserv from "../components/services/contserv";
import { toast } from "react-toastify";

class Contact extends Component {
  state = {
    cont: {
      name: "",
      phno: "",
      email: "",
      message: "",
    },
  };

  handlechange = ({ currentTarget: input }) => {
    const cont = { ...this.state.cont };
    cont[input.name] = input.value;
    this.setState({ cont });
  };

  handcont = async (e) => {
    e.preventDefault();
    const contdata = await contserv.addcont(this.state.cont);
    console.log(contdata);
    if (contdata) {
      toast("succefully sent your service log ");
    }
    this.props.history.push("/");
  };

  render() {
    return (
      <div>
        <button
          className="btn btn-outline-secondary"
          style={{ alignContent: "left", marginLeft: "0px" }}
          onClick={this.props.history.goBack}
        >
          Dashboard
        </button>

        <br />

        <div class="container">
          <div class="card border-dark mb-8" style={{ width: "auto" }}>
            <div class="card-header" style={{ textAlign: "center" }}>
              <strong>CONTACT US</strong>
            </div>
            <div class="card-body text-dark">
              <div class="row">
                <div class="col-lg-2">
                  <br />
                  <br />
                  <br />
                  <br />
                  <img src={img} alt="" />
                </div>{" "}
                <div class="col-lg-8">
                  <h3 style={{ color: "#4c888a" }}>Connect with Us:</h3>
                  <br />
                  <div style={{ textAlign: "center" }}>
                    <strong>Call support (wrking hours): 7995830577</strong>
                    <br />
                    <strong>Email : dlokesh900@gmail.com</strong>
                  </div>
                  <br />
                  <form style={{ textAlign: "left" }} onSubmit={this.handcont}>
                    <h6 style={{ textAlign: "center" }}>
                      Log a service request
                    </h6>
                    <div class="form-group" style={{ textAlign: "left" }}>
                      <label
                        for="exampleInputEmail1"
                        style={{ textAlign: "left" }}
                      >
                        Name
                      </label>
                      <input
                        class="form-control"
                        name="name"
                        value={this.state.cont.name}
                        onChange={this.handlechange}
                      />
                    </div>
                    <div class="form-group">
                      <label
                        for="exampleInputEmail1"
                        style={{ textAlign: "left" }}
                      >
                        Mobile
                      </label>
                      <input
                        class="form-control"
                        name="phno"
                        value={this.state.cont.phno}
                        onChange={this.handlechange}
                      />
                    </div>
                    <div class="form-group">
                      <label
                        for="exampleInputEmail1"
                        style={{ texralign: "left" }}
                      >
                        Email
                      </label>
                      <input
                        type="text"
                        class="form-control"
                        name="email"
                        value={this.state.cont.email}
                        onChange={this.handlechange}
                      />
                    </div>
                    <div class="form-group">
                      <label for="exampleFormControlTextarea1">Message</label>
                      <textarea
                        class="form-control"
                        name="message"
                        value={this.state.cont.message}
                        onChange={this.handlechange}
                        rows="3"
                      ></textarea>
                    </div>
                    <button
                      style={{ alignContent: "center" }}
                      type="submit"
                      class="btn btn-primary"
                    >
                      Submit
                    </button>
                  </form>
                </div>
                <div className="col-lg-2"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Contact;
