import React, { Component } from "react";
import { Link } from "react-router-dom";
import Input from "../common/input";
import * as marchent from "../services/marchent";
import jwtDecode from "jwt-decode";
import { toast } from "react-toastify";

class Addstore extends Component {
  state = {
    store: {
      storename: "",
      busname: "",
      address: "",
      ownname: "",
      city: "",
      state: "",
      zip: "",
      shopdesc: "",
      shopcontact: "",
      email: "",
      marchent_id: "",
    },
  };

  handlechange = ({ currentTarget: input }) => {
    const store = { ...this.state.store };

    store[input.name] = input.value;

    this.setState({ store });
    //console.log(store);
  };
  handlesubmit = async (e) => {
    e.preventDefault();

    const jwt = localStorage.getItem("token");
    const user = jwtDecode(jwt);

    const id = user._id;

    console.log(this.state.store);
    // user = this.user;

    const response = await marchent.addstore(this.state.store, id);
    if (response) {
      toast("store added successfully");
    }

    this.props.history.push("/storedetails");
  };
  render() {
    return (
      <div className="row">
        <div className="col-lg-3 card">
          <br />

          <div
            className="list-group"
            style={{ paddingleft: "10px", border: "2px solid  #60a86e" }}
          >
            <a
              style={{ backgroundColor: " #60a86e" }}
              href="/verifycust"
              className="list-group-item list-group-item-action active"
            >
              Marchent options
            </a>

            <Link
              className="nav-link Active list-group-item list-group-item-action"
              to="/verifycust"
            >
              Verify Customer
            </Link>
            <Link
              className="nav-link Active list-group-item list-group-item-action"
              to="/addstore"
            >
              Add store
            </Link>
            <Link
              className="nav-link Active list-group-item list-group-item-action"
              to="/custhistory"
            >
              Customers History
            </Link>
            <Link
              className="nav-link Active list-group-item list-group-item-action"
              to="/storedetails"
            >
              Store details
            </Link>
          </div>
        </div>
        <div className="col-lg-9 card">
          <br />

          <div
            className="card"
            style={{ textAlign: "left ", border: "4px solid  #60a86e" }}
          >
            <h5
              className="card-header"
              style={{ textAlign: "center", backgroundColor: "#60a86e" }}
            >
              AddStore
            </h5>
            <div className="card-body">
              <form onSubmit={this.handlesubmit}>
                <div className="row">
                  <div className="col-lg-2"></div>
                  <div className="col-lg-4">
                    <Input
                      autoFocus
                      name="storename"
                      value={this.state.store.storename}
                      lable="Store Name"
                      onChange={this.handlechange}
                    ></Input>

                    <Input
                      autoFocus
                      name="ownname"
                      value={this.state.store.ownname}
                      lable="Owener Name"
                      onChange={this.handlechange}
                    ></Input>
                    <div className="form-group">
                      <label id="exampleInputEmail1">Address</label>
                      <select
                        className="form-control "
                        type="text"
                        name="address"
                        onChange={this.handlechange}
                      >
                        <option>Select Address</option>
                        <option>kompally</option>

                        <option>bownpally</option>

                        <option>secundrabad</option>

                        <option>uppal</option>
                        <option>kukatpally</option>
                      </select>
                    </div>

                    <Input
                      autoFocus
                      name="busname"
                      value={this.state.store.busname}
                      lable="Business Name"
                      onChange={this.handlechange}
                    ></Input>

                    <Input
                      autoFocus
                      name="city"
                      value={this.state.store.city}
                      lable="City"
                      onChange={this.handlechange}
                    ></Input>
                  </div>
                  <div className="col-lg-4">
                    <Input
                      name="state"
                      value={this.state.store.state}
                      lable="State"
                      onChange={this.handlechange}
                    ></Input>
                    <Input
                      autoFocus
                      name="zip"
                      value={this.state.store.zip}
                      lable="Zip"
                      onChange={this.handlechange}
                    ></Input>

                    <Input
                      autoFocus
                      name="shopcontact"
                      value={this.state.store.shopcontact}
                      lable="Store contact"
                      onChange={this.handlechange}
                    ></Input>
                    <Input
                      autoFocus
                      name="email"
                      value={this.state.store.email}
                      lable="Email"
                      onChange={this.handlechange}
                    ></Input>
                    <div class="form-group">
                      <label for="exampleFormControlTextarea1">
                        Store Discription
                      </label>
                      <textarea
                        name="shopdesc"
                        class="form-control"
                        value={this.state.store.shopdesc}
                        onChange={this.handlechange}
                        id="exampleFormControlTextarea1"
                        rows="3"
                      ></textarea>
                    </div>
                  </div>
                  <div className="col-lg-2"></div>
                  <button
                    type="submit"
                    className="btn btn-dark"
                    style={{
                      marginLeft: "45%",
                      backgroundColor: "#60a86e",
                    }}
                  >
                    Add store
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Addstore;
