import side from "./sidebar";
import { Link } from "react-router-dom";
import React, { Component } from "react";
import Input from "../common/input";
import joi from "joi-browser";
import * as userService from "../services/userService";
import axios from "axios";
import { toast } from "react-toastify";

class Reguser extends Component {
  state = {
    account: {
      name: "",
      phno: "",
      address: "",
      rationno: "",
      email: "",
      pass: "",
    },
  };

  // schema = {
  //   username: joi.string().required(),
  //   password: joi.string().required(),
  // };
  // username = React.createRef();
  // phno = React.createRef();
  // address = React.createRef();
  // rationno = React.createRef();
  // email = React.createRef();
  // pass = React.createRef();

  handlechange = ({ currentTarget: input }) => {
    const account = { ...this.state.account };
    account[input.name] = input.value;
    this.setState({ account });
  };

  handleregister = async (e) => {
    e.preventDefault();

    console.log(this.state.account);

    const response = await userService.register(this.state.account);
    localStorage.setItem("token", response.headers["x-auth-token"]);
    console.log(response);
    if (response) {
      toast(" Registered sucsessfully and you are loged in");
    }
    // this.props.history.push("/nearstore");
    window.location = "/nearstore";
  };
  render() {
    return (
      <div>
        <div className="row">
          <div className="col-lg-3 card">
            <br />

            <div
              className="list-group"
              style={{ paddingleft: "10px", border: "1.5px solid  #9ea8a3" }}
            >
              <a
                href="/marchentreg"
                className="card-header"
                style={{ WebkitTextFillColor: "black" }}
              >
                Select Regestration Type
              </a>

              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/userreg"
              >
                <b> Register as customer</b>
              </Link>
              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/marchentreg"
              >
                <b> Register as marchent</b>
              </Link>
            </div>
          </div>
          <div className="col-lg-9 card">
            <br />
            <div className="row" style={{ marginTop: "100 px" }}>
              <div className="col-lg-3"></div>
              <div className="col-lg-6">
                <div
                  className="card"
                  style={{ textAlign: "left ", border: "2px solid  #9ea8a3" }}
                >
                  <h5 className="card-header" style={{ textAlign: "center" }}>
                    User SignUp
                  </h5>
                  <div className="card-body">
                    <form onSubmit={this.handleregister}>
                      <Input
                        name="name"
                        value={this.state.account.name}
                        lable="Name"
                        onChange={this.handlechange}
                      ></Input>

                      <Input
                        name="phno"
                        value={this.state.account.phno}
                        lable="Phone Number"
                        onChange={this.handlechange}
                      ></Input>

                      <div className="form-group">
                        <label id="exampleInputEmail1">Address</label>
                        <select
                          className="form-control "
                          type="text"
                          name="address"
                          onChange={this.handlechange}
                        >
                          <option>Select Address</option>
                          <option>kompally</option>

                          <option>bownpally</option>

                          <option>secundrabad</option>

                          <option>uppal</option>
                          <option>kukatpally</option>
                        </select>
                      </div>

                      <Input
                        name="rationno"
                        value={this.state.account.rationno}
                        lable=" Government id  No"
                        onChange={this.handlechange}
                        place="ex : pan card , ration card"
                      ></Input>

                      <Input
                        name="email"
                        value={this.state.account.email}
                        lable="Email address"
                        onChange={this.handlechange}
                      ></Input>
                      <Input
                        name="pass"
                        value={this.state.account.pass}
                        lable="Password"
                        onChange={this.handlechange}
                      ></Input>

                      <button
                        type="submit"
                        className="btn btn-dark"
                        style={{
                          marginLeft: "45%",
                        }}
                      >
                        Register
                      </button>
                    </form>
                  </div>
                </div>
              </div>
              <div className="col-lg- 3"></div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default Reguser;
