import http from "./httpService";

const url = "https://safebuyback.herokuapp.com/addslot";

export function addslot(data, user) {
  return http.post(url, {
    name: data.name,
    phno: data.phno,
    address: user.address,
    rationno: user.rationno,
    age: data.age,
    gender: data.gender,
    time: data.time,
    email: data.email,
    saddress: data.saddress,
    storename: data.storename,
    user_id: data.user_id,
    marchent_id: data.marchent_id,
  });
}
