import { Route, Switch, NavLink, Link } from "react-router-dom";
import React, { Component } from "react";
import img from "../userpages/download.jpeg";

const displaystores = ({ stores }) => {
  return stores.data.map((store, index) => (
    <div
      key={store._id}
      className="card"
      style={{
        width: "18rem",
        margin: "20px",
        marginLeft: "60px",
        border: "1px solid #60a86e",
      }}
    >
      <div className="card-body">
        <h5 className="card-title">{store.storename}</h5>
        <h6 className="card-subtitle mb-2 text-muted">{store.address}</h6>
        <img src={img} className="card-img-top" alt="..." />
        <p className="card-text">{store.shopdesc}</p>
        <p className="card-text">contact no : {store.shopcontact}</p>
        <Link
          className="nav-link Active list-group-item list-group-item-action btn"
          to={{
            pathname: "/bookslot",
            state: {
              store: store,
              marchent_id: store.marchent_id,
            },
          }}
          style={{ color: "#60a86e" }}
        >
          <b> Book Your Slot</b>
        </Link>
      </div>
    </div>
  ));
};

export default displaystores;
