import React, { Component } from "react";
import { Link } from "react-router-dom";

import * as storeservice from "../services/getstoredata";
import Skeleton from "@yisheng90/react-loading";
import jwtDecode from "jwt-decode";
import Storecard from "./storedetailscard";

class Nearstore extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: null,
    };
  }

  componentWillMount() {
    this.data();
  }
  data = async () => {
    const jwt = localStorage.getItem("token");
    const user = jwtDecode(jwt);
    const id = user._id;

    const result = await storeservice.getstoredata(id);

    this.setState({ data: result.data });
    console.log(result);
    console.log(this.state.data);
  };

  render() {
    return (
      <div>
        <div className="row">
          <div className="col-lg-3 card">
            <br />

            <div
              className="list-group"
              style={{ paddingleft: "10px", border: "2px solid  #60a86e" }}
            >
              <a
                style={{ backgroundColor: " #60a86e" }}
                href="/verifycust"
                className="list-group-item list-group-item-action active"
              >
                Marchent options
              </a>

              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/verifycust"
              >
                Verify Customer
              </Link>
              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/addstore"
              >
                Add store
              </Link>
              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/custhistory"
              >
                Customers History
              </Link>
              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/storedetails"
              >
                Store details
              </Link>
            </div>
          </div>
          <div className="col-lg-9 card">
            <hr />
            <h4 style={{ color: "#48ab85", textalign: "left" }}>
              Your Store details
            </h4>
            <hr />
            <div className="row" style={{ marginTop: "100 px" }}>
              <div className="col-lg-1"></div>

              <div className="col-lg-10">
                {this.state.data ? (
                  <Storecard stores={this.state.data} />
                ) : (
                  <div>
                    <Skeleton
                      style={{
                        color: "#48ab85",
                        textalign: "center",
                        marginLeft: "400px",
                        aligncontent: "center",
                      }}
                      width="200px"
                      height="25px"
                    ></Skeleton>
                    <h3
                      style={{
                        color: "#48ab85",
                        textalign: "center",
                        marginLeft: "150px",
                      }}
                    >
                      loading your stores dtails
                    </h3>
                  </div>
                )}{" "}
              </div>
              <div className="col-lg- 1"></div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Nearstore;
