import React, { Component } from "react";
import DateTimePicker from "react-datetime-picker";
import { Link } from "react-router-dom";
import img from "../userpages/download.jpeg";
import Input from "../common/input";
import jwtDecode from "jwt-decode";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { registerLocale, setDefaultLocale } from "react-datepicker";
import es from "date-fns/locale/es";
import TimePicker from "react-time-picker";

import Storecard from "../marchentpages/storedetailscard";
import * as order from "../services/orderservice";
import * as userserv from "../services/getuser";
import * as updateserv from "../services/updateslot";
import * as timeupdate from "../services/updatetcountserv";
import { toast } from "react-toastify";

registerLocale("es", es);
class Slotbook extends Component {
  state = {
    store: null,
    Date: "",
    time: "10:00",
    slotcount: null,
    slot: {
      name: "",
      age: "",
      gender: "",
      time: "",
      phno: "",
      email: "",
    },
  };
  handletChange = async (date) => {
    const date1 = date.toString().substring(0, 25);
    this.setState({
      Date: date,
    });

    console.log(date1);
    const userid1 = this.props.location.state;
    const marchent_id = userid1.marchent_id;
    const s = await timeupdate.tcupdate(
      marchent_id,
      date.toString().substring(16, 18)
    );
    console.log(s, "res");
  };

  ontChange = async (time) => {
    this.setState({ time });
  };

  componentWillMount() {
    const store = this.do();
    this.setState({ store });
  }
  do = async () => {
    const data = this.props.location.state.store;
    console.log("data", data, "data");
    this.setState({ ...this.state.store, data });
    console.log(this.state.store);
    const jwt = localStorage.getItem("token");
    const user = jwtDecode(jwt);

    const user_id = user._id;
    const userdata = await userserv.getuser(user_id);
    const val = userdata.data[0].slotcount;
    if (userdata.data[0].slotcount >= 3) {
      this.setState({ slotcount: val });
    }
    console.log("user count", this.state.slotcount);

    return data;
  };

  handlechange = ({ currentTarget: input }) => {
    const slot = { ...this.state.slot };
    slot[input.name] = input.value;
    this.setState({ slot });
  };

  handlslot = async (e) => {
    const userid1 = this.props.location.state;
    console.log("in store ", userid1);

    e.preventDefault();
    console.log(this.state.slot);
    const jwt = localStorage.getItem("token");
    const user = jwtDecode(jwt);

    const user_id = user._id;
    const marchent_id = userid1.marchent_id;
    const storename = userid1.store.storename;
    const address = userid1.store.address;
    console.log("a , add", storename, address);

    const ress = await updateserv.updateslot(user_id);
    console.log(ress, "user slot count");

    const response = await order.addorder(
      this.state.slot,
      user_id,
      marchent_id,
      userid1.store.address,
      userid1.store.storename,
      this.state.Date.toString().substring(0, 33)
    );
    if (response) {
      toast("your slot booked successfully");
    }
    this.props.history.push("/currentslots");
  };

  render() {
    return (
      <div>
        {/* <div>
          <DateTimePicker onChange={this.onChange} value={this.state.date} />
        </div> */}
        <div className="row">
          <div className="col-lg-3 card">
            <br />

            <div
              class="list-group"
              style={{ paddingleft: "10px", border: "2px solid  #60a86e" }}
            >
              <a
                style={{ backgroundColor: " #60a86e" }}
                href="/nearstore"
                class="list-group-item list-group-item-action active"
              >
                User options
              </a>

              <Link
                class="nav-link Active list-group-item list-group-item-action"
                to="/nearstore"
              >
                Stores Near You
              </Link>
              <Link
                class="nav-link Active list-group-item list-group-item-action"
                to="/currentslots"
              >
                Avilable Slots{" "}
              </Link>
              <Link
                class="nav-link Active list-group-item list-group-item-action"
                to="/bookinghistory"
              >
                shoping history
              </Link>
            </div>
          </div>
          <div className="col-lg-9 card">
            <br />
            <h4 style={{ color: "#48ab85", textalign: "left" }}>
              booking slot for following store
            </h4>

            <div className="row">
              <div className="col-lg-2"></div>

              <div className="col-lg-8">
                <div>
                  <br />
                  <div
                    key={this.props.location.state.store._id}
                    className="card"
                    style={{ textAlign: "left ", border: "4px solid #48ab85" }}
                  >
                    <h5
                      className="card-header"
                      style={{
                        textAlign: "center",
                        backgroundColor: "#48ab85",
                      }}
                    >
                      {this.props.location.state.store.storename}
                    </h5>
                    <div className="card-body">
                      <div className="row">
                        <div className="col-lg-6">
                          <ul class="list-group list-group-flush">
                            <li class="list-group-item">
                              <b>Storename :</b>
                              <span
                                style={{
                                  textalign: "right",
                                }}
                              >
                                {" "}
                                {this.props.location.state.store.storename}
                              </span>
                            </li>
                            <li class="list-group-item">
                              <b>Busness Name:</b>
                              <span
                                style={{
                                  textalign: "right",
                                }}
                              >
                                {" "}
                                {this.props.location.state.store.busname}
                              </span>
                            </li>
                            <li class="list-group-item">
                              <b>Owner Name:</b>
                              <span
                                style={{
                                  textalign: "right",
                                }}
                              >
                                {" "}
                                {this.props.location.state.store.ownname}
                              </span>
                            </li>
                            <li class="list-group-item">
                              <b>Email:</b>
                              <span
                                style={{
                                  textalign: "right",
                                }}
                              >
                                {" "}
                                {this.props.location.state.store.email}
                              </span>
                            </li>
                          </ul>
                        </div>
                        <div className="col-lg-6">
                          <ul class="list-group list-group-flush">
                            <li class="list-group-item">
                              <b>Address:</b>
                              <span
                                style={{
                                  textalign: "right",
                                }}
                              >
                                {" "}
                                {this.props.location.state.store.address}
                              </span>
                            </li>
                            <li class="list-group-item">
                              <b>City</b>
                              <span
                                style={{
                                  textalign: "right",
                                }}
                              >
                                {" "}
                                {this.props.location.state.store.city}
                              </span>
                            </li>
                            <li class="list-group-item">
                              <b>Zip code</b>
                              <span
                                style={{
                                  textalign: "right",
                                }}
                              >
                                {" "}
                                {this.props.location.state.store.zip}
                              </span>
                            </li>
                            <li class="list-group-item">
                              <b>Shop Description</b>
                              <span
                                style={{
                                  textalign: "right",
                                }}
                              >
                                {" "}
                                {this.props.location.state.store.shopdesc}
                              </span>
                            </li>
                          </ul>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <br />
                <div
                  class="card"
                  style={{ textAlign: "left ", border: "4px solid  #60a86e" }}
                >
                  <h5
                    class="card-header"
                    style={{ textAlign: "center", backgroundColor: "#60a86e" }}
                  >
                    BOOK SLOT
                  </h5>
                  <div class="card-body">
                    <div className="row">
                      <div className="col-lg-1"></div>
                      <div className="col-lg-10">
                        {!this.state.slotcount && (
                          <React.Fragment>
                            <form onSubmit={this.handlslot}>
                              <Input
                                autoFocus
                                name="name"
                                value={this.state.slot.name}
                                lable="Enter Name"
                                onChange={this.handlechange}
                              ></Input>
                              <Input
                                autoFocus
                                name="age"
                                value={this.state.slot.age}
                                lable="Enter Age"
                                onChange={this.handlechange}
                              ></Input>
                              <div class="form-group">
                                <label>Select Gender</label>
                                <select
                                  class="form-control "
                                  name="gender"
                                  onChange={this.handlechange}
                                >
                                  <option st>Select gender</option>
                                  <option>Male</option>

                                  <option>female</option>

                                  <option>others</option>
                                </select>
                              </div>
                              <span></span>
                              <div class="form-group">
                                <label>Select Time slot</label>
                                {/* <select
                              class="form-control "
                              name="time"
                              onChange={this.handlechange}
                            >
                              <option st>Select Time</option>
                              <option>10 AM</option>
                              <option>11 AM</option>
                              <option>12 Pm</option>
                              <option>01 PM</option>
                              <option>02 PM</option>
                              <option>03 PM</option>
                              <option>04 PM</option>
                              <option>05 PM</option>
                            </select> */}
                                {/* <TimePicker
                              onChange={this.ontChange}
                              value={this.state.time}
                            /> */}
                                <DatePicker
                                  value={this.state.Date}
                                  selected={this.state.Date}
                                  onChange={this.handletChange}
                                  locale="es"
                                  showTimeSelect
                                  dateFormat="Pp"
                                />
                              </div>
                              <Input
                                autoFocus
                                name="phno"
                                value={this.state.slot.phno}
                                lable="Phone number"
                                onChange={this.handlechange}
                              ></Input>{" "}
                              <Input
                                autoFocus
                                name="email"
                                value={this.state.slot.email}
                                lable="Email Address"
                                onChange={this.handlechange}
                              ></Input>
                              <button
                                type="submit"
                                class="btn btn-dark"
                                style={{
                                  marginLeft: "45%",
                                  backgroundColor: "#60a86e",
                                }}
                              >
                                conform Slot
                              </button>
                            </form>
                          </React.Fragment>
                        )}
                        {this.state.slotcount && (
                          <React.Fragment>
                            <h5 style={{ color: "red" }}>
                              Your weekly slot limet reached you booked 3 slots
                              in this week please come back next week
                            </h5>
                            <h4 style={{ color: "yellow" }}>
                              You can not book more than 3 slots in a week
                            </h4>
                          </React.Fragment>
                        )}
                      </div>

                      <div className="col-lg-1"></div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-g-2"></div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Slotbook;
