import http from "./httpService";

const url = "https://safebuyback.herokuapp.com/muserregister";

export function register(user) {
  return http.post(url, {
    storename: user.storename,
    name: user.name,
    address: user.address,
    phno: user.phno,
    email: user.email,
    pass: user.pass,
  });
}
