import React, { Component } from "react";

import "./App.css";
import { ToastContainer } from "react-toastify";
import jwtDecode from "jwt-decode";

import "react-toastify/dist/ReactToastify.css";
import Navbar from "./components/navbar";
import Fotter from "./components/footer";
import help from "./components/help";
import contactus from "./components/contactus";
import home from "./components/home";
import userreg from "./components/registration/userreg";
import login from "./components/login/login";
import marchentreg from "./components/registration/marchentreg";
import marchentpage from "./components/marchentpages/marchentpage";
import addstore from "./components/marchentpages/addstore";
import verifycust from "./components/marchentpages/verifycust";
import datea from "./components/marchentpages/date";
import custhistory from "./components/marchentpages/custhistory";
import storedetails from "./components/marchentpages/storedata";
import nearshos from "./components/userpages/nearshops";
import bookinghistory from "./components/userpages/shoping history";
import bookslot from "./components/userpages/bookslot";
import logout from "./components/login/logout";
import conformslot from "./components/marchentpages/conformslot";
import currentslot from "./components/userpages/currentslots";

import { Route } from "react-router-dom";
import date from "./components/marchentpages/date";
import * as orders from "./components/services/deleteallordersserv";

class App extends Component {
  state = {};

  componentDidMount() {
    try {
      const jwt = localStorage.getItem("token");
      const user = jwtDecode(jwt);
      this.setState({ user });
    } catch (ex) {}
  }

  render() {
    return (
      <div className="App">
        <ToastContainer />
        <Navbar user={this.state.user} />
        <Route path="/date" component={datea} />
        <Route path="/login" component={login} />
        <Route path="/help" component={help} />
        <Route path="/contactus" component={contactus} />
        <Route path="/" exact component={home}></Route>
        <Route path="/userreg" component={userreg} />
        <Route path="/marchentreg" component={marchentreg} />
        <Route path="/marchentpage" component={marchentpage} />
        <Route path="/addstore" component={addstore} />
        <Route path="/verifycust" component={verifycust} />
        <Route path="/custhistory" component={custhistory} />
        <Route path="/storedetails" component={storedetails} />{" "}
        <Route path="/nearstore" component={nearshos} />
        <Route path="/bookinghistory" component={bookinghistory} />
        <Route path="/bookslot" component={bookslot} />
        <Route path="/logout" component={logout} />
        <Route path="/conformslot" component={conformslot} />
        <Route path="/currentslots" component={currentslot} />
        <Fotter></Fotter>
      </div>
    );
  }
}

export default App;
