import http from "./httpService";

const url = "https://safebuyback.herokuapp.com/addstore";

export function addstore(user, id) {
  return http.post(url, {
    storename: user.storename,
    busname: user.busname,
    address: user.address,
    ownname: user.ownname,
    city: user.city,
    state: user.state,
    zip: user.zip,
    shopdesc: user.shopdesc,
    shopcontact: user.shopcontact,
    email: user.email,
    marchent_id: id,
  });
}
