import React, { Component } from "react";
import DateTimePicker from "react-datetime-picker";
import { Link } from "react-router-dom";
import img from "../userpages/download.jpeg";
import Input from "../common/input";
import jwtDecode from "jwt-decode";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { registerLocale, setDefaultLocale } from "react-datepicker";
import es from "date-fns/locale/es";
import TimePicker from "react-time-picker";
import "F:/Work_Space/my_work/Projects/safebuy/safebuy/src/App.css";
import Ordercard from "./slotcard";

import Storecard from "../marchentpages/storedetailscard";
import * as gtall from "../services/getallowuserv";
import * as storeservice from "../services/getstoredata";
import * as userserv from "../services/getuser";

registerLocale("es", es);
class Currentslots extends Component {
  state = {
    store: null,
    class: "",
    data1: null,
    len: 3,
    len1: null,
    m1: null,
    m2: null,
    m3: null,
    user_id: null,
  };
  componentWillMount() {
    this.do();
  }
  do = async () => {
    const jwt = localStorage.getItem("token");
    const user = jwtDecode(jwt);
    const id = user._id;
    const data1 = await gtall.getslots(id);
    const user2 = await userserv.getuser(id);
    console.log(user2.data[0].slotcount, "users loki");

    console.log(data1.data.length, "length");
    this.setState({ len: user2.data[0].slotcount });
    this.setState({ len1: user2.data[0].slotcount * 330 });

    this.setState({ data1: data1.data });
    if (data1.data[0] === undefined) {
      return;
    } else {
      const result = await storeservice.getstoredata(data1.data[0].marchent_id);
      this.setState({ m1: result.data[0] });
    }
    if (data1.data[1] === undefined) {
      return;
    } else {
      const result = await storeservice.getstoredata(data1.data[1].marchent_id);
      this.setState({ m2: result.data[0] });
    }
    if (data1.data[2] === undefined) {
      return;
    } else {
      const result = await storeservice.getstoredata(data1.data[2].marchent_id);
      this.setState({ m3: result.data[0] });
    }

    console.log(this.state.m1, this.state.m2, this.state.m3);
  };
  render() {
    return (
      <div>
        {/* <div>
          <DateTimePicker onChange={this.onChange} value={this.state.date} />
        </div> */}
        <div className="row">
          <div className="col-lg-3 card">
            <br />

            <div
              class="list-group"
              style={{ paddingleft: "10px", border: "2px solid  #60a86e" }}
            >
              <a
                style={{ backgroundColor: " #60a86e" }}
                href="/nearstore"
                class="list-group-item list-group-item-action active"
              >
                User options
              </a>

              <Link
                class="nav-link Active list-group-item list-group-item-action"
                to="/nearstore"
              >
                Stores Near You
              </Link>
              <Link
                class="nav-link Active list-group-item list-group-item-action"
                to="/currentslots"
              >
                Avilable Slots{" "}
              </Link>

              <Link
                class="nav-link Active list-group-item list-group-item-action"
                to="/bookinghistory"
              >
                shoping history
              </Link>
            </div>
          </div>
          <div className="col-lg-9 card">
            <div className="row">
              <div className="col-lg-1"></div>
              <div className="col-lg-10">
                <br />
                <hr />
                <h4 className="mycolor">
                  Slotes used this week : {this.state.len} of 3{" "}
                </h4>
                <hr />
                <div class="progress">
                  <div
                    className="progress-bar bg-success"
                    role="progressbar"
                    style={{
                      width: this.state.len1,
                      valuenow: "25",
                      valuemin: "0",
                      valuemax: "100",
                    }}
                  ></div>
                </div>

                <hr />

                {this.state.data1 ? (
                  <Ordercard
                    orders={this.state.data1}
                    data={[this.state.m1, this.state.m2, this.state.m3]}
                    len={this.len1}
                  />
                ) : (
                  <div>
                    <h6
                      style={{
                        color: "#48ab85",
                        textalign: "center",
                      }}
                    >
                      No current slots available{" "}
                    </h6>
                  </div>
                )}
              </div>
              <div className="col-lg-1"></div>
            </div>
            <br />
            <br />
          </div>
        </div>
      </div>
    );
  }
}

export default Currentslots;
