import React from "react";
import { Link } from "react-router-dom";

const marchentpage = () => {
  return (
    <div>
      <div className="row">
        <div className="col-lg-3 card">
          <br />

          <div
            class="list-group"
            style={{ paddingleft: "10px", border: "2px solid  #60a86e" }}
          >
            <a
              style={{ backgroundColor: " #60a86e" }}
              href="/marchentpage"
              class="list-group-item list-group-item-action active"
            >
              Marchent options
            </a>

            <Link
              class="nav-link Active list-group-item list-group-item-action"
              to="/verifycust"
            >
              Verify Customer
            </Link>
            <Link
              class="nav-link Active list-group-item list-group-item-action"
              to="/addstore"
            >
              Add store
            </Link>
            <Link
              class="nav-link Active list-group-item list-group-item-action"
              to="/custhistory"
            >
              Customers History
            </Link>
            <Link
              class="nav-link Active list-group-item list-group-item-action"
              to="/storedetails"
            >
              Store details
            </Link>
          </div>
        </div>
        <div className="col-lg-9 card">
          <br />
          <div className="row" style={{ marginTop: "100 px" }}>
            <div className="col-lg-1"></div>

            <div className="col-lg-8">
              <div
                class="card"
                style={{ textAlign: "left ", border: "4px solid  #60a86e" }}
              >
                <h5
                  class="card-header"
                  style={{ textAlign: "center", backgroundColor: "#60a86e" }}
                >
                  Marchent SignUp
                </h5>
                <div class="card-body">
                  <form>
                    <div class="form-group">
                      <label for="exampleInputEmail1">Store Name</label>
                      <input
                        type="email"
                        class="form-control"
                        id="exampleInputEmail1"
                        aria-describedby="emailHelp"
                      />
                    </div>
                    <div class="form-group">
                      <label for="exampleInputEmail1">Marchent Name</label>
                      <input
                        type="email"
                        class="form-control"
                        id="exampleInputEmail1"
                        aria-describedby="emailHelp"
                      />
                    </div>
                    <div class="form-group">
                      <label>Address</label>
                      <select class="form-control ">
                        <option st>Select Address</option>
                        <option>kompally</option>

                        <option>bownpally</option>

                        <option>secundrabad</option>

                        <option>uppal</option>
                        <option>kukatpally</option>
                      </select>
                    </div>
                    <div class="form-group">
                      <label for="exampleInputEmail1">Phone number</label>
                      <input
                        type="email"
                        class="form-control"
                        id="exampleInputEmail1"
                        aria-describedby="emailHelp"
                      />
                    </div>
                    <div class="form-group">
                      <label for="exampleInputEmail1">Email address</label>
                      <input
                        type="email"
                        class="form-control"
                        id="exampleInputEmail1"
                        aria-describedby="emailHelp"
                      />
                    </div>

                    <div class="form-group">
                      <label for="exampleInputPassword1">Password</label>
                      <input
                        type="password"
                        class="form-control"
                        id="exampleInputPassword1"
                      />
                    </div>

                    <button
                      type="submit"
                      class="btn btn-dark"
                      style={{ marginLeft: "45%", backgroundColor: "#60a86e" }}
                    >
                      Register
                    </button>
                  </form>
                </div>
              </div>
            </div>
            <div className="col-lg- 1"></div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default marchentpage;
