import http from "./httpService";

const url = "https://safebuyback.herokuapp.com/addorder";

export function addorder(user, id, id2, add, sname, time) {
  return http.post(url, {
    name: user.name,
    age: user.age,
    gender: user.gender,
    time: time,
    phno: user.phno,
    email: user.email,
    saddress: add,
    storename: sname,
    user_id: id,
    marchent_id: id2,
  });
}
