import React, { Component } from "react";
import { Link } from "react-router-dom";
import * as order from "../services/searchorderservice";
import * as allordersder from "../services/getallordersservice";
import * as timecount from "../services/timecountinitserv";

import jwtDecode from "jwt-decode";
import Ordercard from "./ordercard";

class Verifycust extends Component {
  state = {
    data: null,
    data1: null,
    order: {
      phno: "",
    },
  };

  componentWillMount() {
    this.allorders();
  }

  allorders = async () => {
    const jwt = localStorage.getItem("token");
    const user = jwtDecode(jwt);
    const id = user._id;

    const data1 = await allordersder.getstoredata(id);
    this.setState({ data1: data1.data });
    await timecount.tcinit(user._id);
  };

  handlechange = ({ currentTarget: input }) => {
    const order = { ...this.state.order };
    order[input.name] = input.value;
    this.setState({ order });
  };
  handlslot = async (e) => {
    e.preventDefault();
    const jwt = localStorage.getItem("token");
    const marchent = jwtDecode(jwt);

    const marchent_id = marchent._id;
    const response = await order.searchorder(
      this.state.order.phno,
      marchent_id
    );
    this.setState({ data: response.data });
  };
  render() {
    return (
      <div>
        <div className="row">
          <div className="col-lg-3 card">
            <br />

            <div
              className="list-group"
              style={{ paddingleft: "10px", border: "2px solid  #60a86e" }}
            >
              <a
                style={{ backgroundColor: " #60a86e" }}
                href="/verifycust"
                className="list-group-item list-group-item-action active"
              >
                Marchent options
              </a>

              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/verifycust"
              >
                Verify Customer
              </Link>
              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/addstore"
              >
                Add store
              </Link>
              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/custhistory"
              >
                Customers History
              </Link>
              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/storedetails"
              >
                Store details
              </Link>
            </div>
          </div>
          <div className="col-lg-9 card">
            <br />
            <div className="row" style={{ marginTop: "100 px" }}>
              <div className="col-lg-1"></div>

              <div className="col-lg-10">
                <div
                  className="card"
                  style={{ textAlign: "left ", border: "4px solid  #60a86e" }}
                >
                  <h5
                    className="card-header"
                    style={{ textAlign: "center", backgroundColor: "#60a86e" }}
                  >
                    Search and Verfy Customer
                  </h5>
                  <div className="card-body">
                    <form onSubmit={this.handlslot}>
                      <div className="form-group">
                        <label id="exampleInputEmail1">Enter Slot Id</label>
                        <input
                          type="text"
                          className="form-control"
                          name="phno"
                          value={this.state.order.phno}
                          onChange={this.handlechange}
                        />
                      </div>
                      <button
                        type="submit"
                        className="btn btn-dark"
                        style={{
                          alignContent: "center",
                          backgroundColor: "#60a86e",
                        }}
                      >
                        Search Slot
                      </button>
                    </form>
                  </div>
                </div>
              </div>
              <div className="col-lg- 1"></div>
            </div>
            <br />
            <hr />
            <h5 style={{ color: "#48ab85", textalign: "left" }}>
              customer details
            </h5>
            <hr />
            <div className="row" style={{ marginTop: "100 px" }}>
              <div className="col-lg-1"></div>

              <div className="col-lg-10">
                {this.state.data ? (
                  <Ordercard orders={this.state.data} />
                ) : (
                  <div>
                    <h6
                      style={{
                        color: "#48ab85",
                        textalign: "center",
                      }}
                    >
                      Enter slot id to get slots -customer phone no is sloit is
                    </h6>
                  </div>
                )}
                <hr />
                <h5
                  style={{
                    color: "#48ab85",

                    marginLeft: "1px",
                  }}
                >
                  All Avilable Slots
                </h5>
                {this.state.data1 ? (
                  <Ordercard orders={this.state.data1} />
                ) : (
                  <div>
                    <h6
                      style={{
                        color: "#48ab85",
                        textalign: "center",
                      }}
                    >
                      Loading all avilable slots{" "}
                    </h6>
                  </div>
                )}
              </div>
              <div className="col-lg- 1"></div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Verifycust;
