import React, { Component } from "react";
import DateTimePicker from "react-datetime-picker";
import { Link } from "react-router-dom";
import img from "../userpages/download.jpeg";
import Display from "../userpages/diplaystore";
import * as storeservice from "../services/getstores";
import Loading from "./loading";
import Skeleton from "@yisheng90/react-loading";

class Nearstore extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: null,
    };
  }

  componentWillMount() {
    this.data();
  }
  data = async () => {
    const result = await storeservice.getstore();

    this.setState({ data: result });
    console.log(result);
    console.log(result);
  };

  render() {
    return (
      <div>
        {/* <div>
          <DateTimePicker onChange={this.onChange} value={this.state.date} />
        </div> */}
        <div className="row">
          <div className="col-lg-3 card">
            <br />

            <div
              className="list-group"
              style={{ paddingleft: "10px", border: "2px solid  #60a86e" }}
            >
              <a
                style={{ backgroundColor: " #60a86e" }}
                href="/nearstore"
                className="list-group-item list-group-item-action active"
              >
                User options
              </a>

              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/nearstore"
              >
                Stores Near You
              </Link>
              <Link
                class="nav-link Active list-group-item list-group-item-action"
                to="/currentslots"
              >
                Avilable Slots{" "}
              </Link>
              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/bookinghistory"
              >
                shoping history
              </Link>
            </div>
          </div>
          <div className="col-lg-9 card">
            <hr />
            <h4 style={{ color: "#48ab85", textalign: "left" }}>
              Stores near you
            </h4>
            <hr />
            <div className="row" style={{ marginTop: "100 px" }}>
              {this.state.data ? (
                <Display stores={this.state.data} />
              ) : (
                <div>
                  <Loading></Loading>
                  <Skeleton
                    style={{
                      color: "#48ab85",
                      textalign: "center",
                      marginLeft: "480px",
                    }}
                    width="200px"
                    height="25px"
                  ></Skeleton>
                  <h3
                    style={{
                      color: "#48ab85",
                      textalign: "center",
                      marginLeft: "480px",
                    }}
                  >
                    loading stores.....
                  </h3>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Nearstore;
