import React, { Component } from "react";
const Storedatacard = ({ stores }) => {
  return stores.map((store) => (
    <div>
      <br />
      <div
        key={store._id}
        className="card"
        style={{ textAlign: "left ", border: "4px solid #48ab85" }}
      >
        <h5
          className="card-header"
          style={{ textAlign: "center", backgroundColor: "#48ab85" }}
        >
          {store.storename}
        </h5>
        <div className="card-body">
          <div className="row">
            <div className="col-lg-6">
              <ul class="list-group list-group-flush">
                <li class="list-group-item">
                  <b>Storename :</b>
                  <span
                    style={{
                      textalign: "right",
                    }}
                  >
                    {" "}
                    {store.storename}
                  </span>
                </li>
                <li class="list-group-item">
                  <b>Busness Name:</b>
                  <span
                    style={{
                      textalign: "right",
                    }}
                  >
                    {" "}
                    {store.busname}
                  </span>
                </li>
                <li class="list-group-item">
                  <b>Owner Name:</b>
                  <span
                    style={{
                      textalign: "right",
                    }}
                  >
                    {" "}
                    {store.ownname}
                  </span>
                </li>
                <li class="list-group-item">
                  <b>Email:</b>
                  <span
                    style={{
                      textalign: "right",
                    }}
                  >
                    {" "}
                    {store.email}
                  </span>
                </li>
              </ul>
            </div>
            <div className="col-lg-6">
              <ul class="list-group list-group-flush">
                <li class="list-group-item">
                  <b>Address:</b>
                  <span
                    style={{
                      textalign: "right",
                    }}
                  >
                    {" "}
                    {store.address}
                  </span>
                </li>
                <li class="list-group-item">
                  <b>City</b>
                  <span
                    style={{
                      textalign: "right",
                    }}
                  >
                    {" "}
                    {store.city}
                  </span>
                </li>
                <li class="list-group-item">
                  <b>Zip code</b>
                  <span
                    style={{
                      textalign: "right",
                    }}
                  >
                    {" "}
                    {store.zip}
                  </span>
                </li>
                <li class="list-group-item">
                  <b>Shop Description</b>
                  <span
                    style={{
                      textalign: "right",
                    }}
                  >
                    {" "}
                    {store.shopdesc}
                  </span>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  ));
};

export default Storedatacard;
