import http from "./httpService";

const url = "https://safebuyback.herokuapp.com/userregister";

export function register(user) {
  return http.post(url, {
    name: user.name,
    phno: user.phno,
    address: user.address,
    rationno: user.rationno,
    email: user.email,
    pass: user.pass,
  });
}
