import React, { Component } from "react";
import { Link } from "react-router-dom";
import * as order from "../services/searchorderservice";
import * as allordersder from "../services/getallordersservice";
import * as slot from "../services/getslotsserv";
import Slotcard from "./slotdatacard";

import jwtDecode from "jwt-decode";
import Ordercard from "./ordercard";

class Customerhistory extends Component {
  state = {
    data: null,
    data1: null,
    order: {
      phno: "",
    },
  };

  componentWillMount() {
    this.allorders();
  }

  allorders = async () => {
    const jwt = localStorage.getItem("token");
    const user = jwtDecode(jwt);
    const id = user._id;

    const data1 = await slot.getslots(id);
    this.setState({ data1: data1.data });
    console.log(data1);
  };
  render() {
    return (
      <div>
        <div className="row">
          <div className="col-lg-3 card">
            <br />

            <div
              className="list-group"
              style={{ paddingleft: "10px", border: "2px solid  #60a86e" }}
            >
              <a
                style={{ backgroundColor: " #60a86e" }}
                href="/verifycust"
                className="list-group-item list-group-item-action active"
              >
                Marchent options
              </a>

              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/verifycust"
              >
                Verify Customer
              </Link>
              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/addstore"
              >
                Add store
              </Link>
              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/custhistory"
              >
                Customers History
              </Link>
              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/storedetails"
              >
                Store details
              </Link>
            </div>
          </div>
          <div className="col-lg-9 card">
            <br />

            <div className="row" style={{ marginTop: "100 px" }}>
              <div className="col-lg-1"></div>

              <div className="col-lg-10">
                <hr />
                <h5
                  style={{
                    color: "#48ab85",

                    marginLeft: "1px",
                  }}
                >
                  Transaction history
                </h5>

                {this.state.data1 ? (
                  <Slotcard slots={this.state.data1} />
                ) : (
                  <div>
                    <h6
                      style={{
                        color: "#48ab85",
                        textalign: "center",
                      }}
                    >
                      Loading all Your Customers{" "}
                    </h6>
                  </div>
                )}
              </div>
              <div className="col-lg- 1"></div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Customerhistory;
