import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import userlogo from "../marchentpages/userlogo.png";

import * as userserv from "../services/getuser";
import * as slotserv from "../services/addslotservice";
import * as deleteserv from "../services/deleteorder";

import jwtDecode from "jwt-decode";
import Ordercard from "./ordercard";

class conformslot extends Component {
  state = {
    data: null,
    user: null,
  };

  componentWillMount() {
    this.do();
  }
  do = async () => {
    const data = this.props.location.state.data;
    this.setState({ ...this.state.data, data });
  };

  handlechange = () => {};
  conformslot = async (e) => {
    console.log(this.props.location.state.data.user_id);
    const user2 = await userserv.getuser(this.state.data.user_id);
    const user = user2.data[0];
    this.setState({ ...this.state.user, user });

    const slot = await slotserv.addslot(this.state.data, user2.data[0]);
    const id = this.props.location.state.data._id;

    const deleted = await deleteserv.deleteorder(id);

    // const deleted = await deleteserv.deleteorder(id);

    this.props.history.push("/verifycust");
  };
  cancleslot = async () => {
    const id = this.props.location.state.data._id;

    const deleted = await deleteserv.deleteorder(id);

    this.props.history.push("/verifycust");
  };
  render() {
    return (
      <div>
        <div className="row">
          <div className="col-lg-3 card">
            <br />

            <div
              className="list-group"
              style={{ paddingleft: "10px", border: "2px solid  #60a86e" }}
            >
              <a
                style={{ backgroundColor: " #60a86e" }}
                href="/verifycust"
                className="list-group-item list-group-item-action active"
              >
                Marchent options
              </a>

              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/verifycust"
              >
                Verify Customer
              </Link>
              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/addstore"
              >
                Add store
              </Link>
              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/custhistory"
              >
                Customers History
              </Link>
              <Link
                className="nav-link Active list-group-item list-group-item-action"
                to="/storedetails"
              >
                Store details
              </Link>
            </div>
          </div>
          <div className="col-lg-9 ">
            <div className="card">
              <hr />
              <div className="row" style={{ marginTop: "100 px" }}>
                <div className="col-lg-1"></div>

                <div className="col-lg-10">
                  <div>
                    <div
                      key={this.state.data._id}
                      className="card"
                      style={{
                        textAlign: "left ",
                        border: "4px solid #48ab85",
                      }}
                    >
                      <h5
                        className="card-header"
                        style={{
                          textAlign: "center",
                          backgroundColor: "#48ab85",
                        }}
                      >
                        <p>Slot: Details</p>
                      </h5>
                      <div className="card-body">
                        <div className="row">
                          <div className="col-lg-3">
                            <img
                              width="100%"
                              height="100%"
                              src={userlogo}
                              alt=""
                            />
                          </div>

                          <div className="col-lg-4">
                            <ul class="list-group list-group-flush">
                              <li class="list-group-item">
                                <b>Customer Name :</b>
                                <span
                                  style={{
                                    textalign: "right",
                                  }}
                                >
                                  {" "}
                                  {this.state.data.name}
                                </span>
                              </li>
                              <li class="list-group-item">
                                <b>Age:</b>
                                <span
                                  style={{
                                    textalign: "right",
                                  }}
                                >
                                  {" "}
                                  {this.state.data.age}
                                </span>
                              </li>
                              <li class="list-group-item">
                                <b>Gender:</b>
                                <span
                                  style={{
                                    textalign: "right",
                                  }}
                                >
                                  {" "}
                                  {this.state.data.gender}
                                </span>
                              </li>
                              <li class="list-group-item">
                                <b>Store Name</b>
                                <span
                                  style={{
                                    textalign: "right",
                                  }}
                                >
                                  {" "}
                                  {this.state.data.storename}
                                </span>
                              </li>
                            </ul>
                          </div>
                          <div className="col-lg-4">
                            <ul class="list-group list-group-flush">
                              <li class="list-group-item">
                                <b>Time</b>
                                <span
                                  style={{
                                    textalign: "right",
                                  }}
                                >
                                  {" "}
                                  {this.state.data.time
                                    .toString()
                                    .substring(0, 25)}
                                </span>
                              </li>
                              <li class="list-group-item">
                                <b>Phone number</b>
                                <span
                                  style={{
                                    textalign: "right",
                                  }}
                                >
                                  {" "}
                                  {this.state.data.phno}
                                </span>
                              </li>
                              <li class="list-group-item">
                                <b>Email</b>
                                <span
                                  style={{
                                    textalign: "right",
                                  }}
                                >
                                  {" "}
                                  {this.state.data.email}
                                </span>
                              </li>
                              <li class="list-group-item">
                                <b>Store Address</b>
                                <span
                                  style={{
                                    textalign: "right",
                                  }}
                                >
                                  {" "}
                                  {this.state.data.saddress}
                                </span>
                              </li>
                            </ul>
                          </div>
                          <hr />
                          <button
                            onClick={this.cancleslot}
                            className="btn btn-danger"
                            style={{
                              marginLeft: "150px",
                              color: "white",
                              textAlign: "center",
                            }}
                          >
                            cancle slot
                          </button>
                          <button
                            onClick={this.conformslot}
                            className="btn"
                            style={{
                              marginLeft: "400px",

                              color: "white",
                              textAlign: "right",
                              backgroundColor: "#48ab85",
                            }}
                          >
                            confom slot
                          </button>
                          <hr />
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-lg- 1"></div>
              </div>
            </div>
          </div>
        </div>
        <div className="col-lg- 1"></div>
      </div>
    );
  }
}

export default conformslot;
